.search-form {
  background-color: #5555e0;
  justify-content: center;
  gap: 5px;
  padding: 10px;
  display: flex;
}

.search-form input {
  width: 200px;
  background-color: #fff;
  border-width: 1px;
  border-color: #f5f5f5;
  border-radius: 5px;
  padding: 5px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #2e2e2e;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
}

.search-form button:hover {
  background-color: #5a5a5a;
  transition: all .7s;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1%;
  display: flex;
}

.photo-card {
  width: 24%;
  box-sizing: border-box;
  background-color: #f5f5f5;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 11px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: 120px;
  object-fit: cover;
  border-radius: 5px;
}

.info {
  text-align: center;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 5px;
  display: flex;
}

@media screen and (max-width: 768px) {
  .photo-card {
    width: 45%;
  }
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #0037ae;
  border: none;
  border-radius: 5px;
  margin: 10px auto 0;
  padding: 5px 20px;
  display: none;
}

.load-more:hover {
  background-color: #1055ec;
  transition: all .3s;
}

